<template>
	<div>
		<center>登录</center>
		<el-form label-position="right" label-width="80px" name='loginForm'>
		  <el-form-item label="账号:">
		    <el-input name="email"></el-input>
		  </el-form-item>
		  <el-form-item label="密码:">
		    <el-input name="pwd" type='password'></el-input>
		  </el-form-item>
		  <el-form-item label="验证码:">
		    <el-input name="yzm"></el-input>
		  </el-form-item>
		  <el-form-item>
		    <el-button type="primary">登陆</el-button>&nbsp;
		    <el-button type="primary">注册</el-button>&nbsp;
		    <el-button type="primary" @click='back'>返回</el-button>
		  </el-form-item>
		</el-form>
	</div>
</template>
<script>
	let thisa = null;
export default{
	data(){
		thisa = this;
		return {};
	},
	methods:{
		back:function(e){
			thisa.$emit('back')
		}
	}
}
</script>


